import React, { useEffect, useState, useContext, useMemo } from 'react';
import { useCallback } from 'react';
import 'react-circular-progressbar/dist/styles.css';
import { useNavigate, useParams } from 'react-router-dom';
import jwtDecode from 'jwt-decode';

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/navigation';
import TokenExpire from '../../components/Modals/TokenExpire';
import TopicCard from '../../components/Student/TopicCard';
import { API_URL } from '../../constant';
import createAxiosJWT from '../../createAxiosJWT';
import { SocketContext } from '../../App';

import nocontent from '../../assets/image/nocontent.png';
import SkillInTopics from '../../components/Student/SkillInTopics';

const axiosJWT = createAxiosJWT();

const topicImage =
    'https://thumbs.dreamstime.com/b/letter-block-word-topic-wood-background-business-concept-170764857.jpg';
const StudentClass = ({ isParent }) => {
    const socket = useContext(SocketContext);
    const navigate = useNavigate();
    const accessToken = localStorage.getItem('access_token');
    const decodedToken = useMemo(() => {
        return accessToken && jwtDecode(accessToken);
    }, [accessToken]);
    const { classId } = useParams();
    const [studentInfo, setStudentInfo] = useState(null);
    const [classInfo, setClassInfo] = useState(null);

    useEffect(() => {
        if (isParent) {
            const getStudentInfo = async () => {
                try {
                    const res = await axiosJWT.get(API_URL + 'parent/student');
                    setStudentInfo(res.data);
                    navigate(`/class/${res.data?.classId}`);
                } catch (err) {
                    console.log(err);
                }
            };
            getStudentInfo();
        } else {
            const getClassOfStudent = async () => {
                const res = await axiosJWT.get(API_URL + 'student/class');
                setClassInfo(res.data);
                if (decodedToken?.classId !== parseInt(classId))
                    navigate(`/class/${res.data?.id || decodedToken?.classId}`);
            };
            getClassOfStudent();
        }

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [decodedToken, classId]);
    const [topicsOfClass, setTopicsOfClass] = useState([]);
    const [searchTerm, setSearchTerm] = useState('');
    const [currentTopic, setCurrentTopic] = useState();
    const [skills, setSkills] = useState([]);
    const [currentTopicId, setCurrentTopicId] = useState('');
    const [topics, setTopics] = useState([]);
    const [isExpired, setIsExpired] = useState(false);

    const getTopicIdUnlocked = (topics) => {
        for (let i = 0; i < topics.length; i++)
            if (topics[i].isUnlock === 1) return topics[i].topicId;
    };
    const getTopicOfClass = useCallback(async () => {
        try {
            let result;
            if (isParent) {
                if (studentInfo) {
                    const topicsOfStudent = await axiosJWT.get(
                        API_URL + `parent/student/${studentInfo?.id}/class/${classId}/topic`
                    );
                    result = topicsOfStudent.data;
                }
            } else {
                const topicsOfStudent = await axiosJWT.get(
                    API_URL + `student-topic/student/class/${classId}`
                );
                result = topicsOfStudent.data;
            }
            if (result) {
                if (!currentTopicId) setCurrentTopicId(getTopicIdUnlocked(result));
                let valueTopics = [];
                for (let i = 0; i < result?.length; ++i) {
                    valueTopics = [
                        ...valueTopics,
                        {
                            id: result[i].topicId,
                            topicName: result[i].topicName,
                            description: result[i].description,
                            topicImg: result[i].topicImg,
                            prerequisiteTopicName: result[i].prerequisiteTopicName,
                            classTopicId: result[i].id,
                        },
                    ];
                }
                setTopics(valueTopics);
                setTopicsOfClass(result);
            }
        } catch (error) {
            console.log(error);
            if (error.response.status === 401) setIsExpired(true);
        }
    }, [classId, currentTopicId, isParent, studentInfo]);

    const getSkillsOfTopic = useCallback(async () => {
        try {
            if (currentTopicId) {
                let res;
                if (currentTopicId && isParent) {
                    res = await axiosJWT.get(
                        API_URL + `parent/student/${studentInfo?.id}/topic/${currentTopicId}`
                    );
                } else {
                    res = await axiosJWT.get(
                        API_URL + `student-skill/student/topic/${currentTopicId}`
                    );
                }
                setSkills(res.data);
                setCurrentTopic(topics.find((topic) => topic.id === currentTopicId) || {});
            }
        } catch (error) {
            console.log(error);
            if (error.response.status === 401) setIsExpired(true);
        }
    }, [currentTopicId, topics, isParent, studentInfo?.id]);

    useEffect(() => {
        getSkillsOfTopic();
    }, [getSkillsOfTopic]);

    useEffect(() => {
        getTopicOfClass();
    }, [getTopicOfClass]);

    useEffect(() => {
        socket?.on('get-handle-request-notification', (data) => {
            if (
                data?.senderId !== decodedToken?.accountId &&
                data?.tableHandle === 'Student_Topic'
            ) {
                getTopicOfClass();
            }
        });
        return () => {
            socket?.off('get-handle-request-notification');
        };
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [socket, decodedToken]);

    return (
        <div className='flex flex-row h-screen'>
            {/* left */}
            <div className='w-[40%] flex flex-col px-5 py-5 gap-6'>
                <div className='flex flex-col gap-3 px-4'>
                    <span className='text-2xl font-medium truncate'>{classInfo?.className}</span>
                    {/* search */}
                    <div className='flex flex-row justify-between items-center w-full'>
                        <input
                            onChange={(e) => setSearchTerm(e.target.value)}
                            className='outline-none rounded-lg text-base px-4 py-2 w-[65%] drop-shadow-md focus:drop-shadow-lg transition-all'
                            placeholder='Search topics'
                        />
                    </div>
                </div>
                {/* filter */}
                {/* courses */}
                <div className='flex flex-col gap-7 px-4 pb-4 overflow-auto scroll-smooth'>
                    {topicsOfClass
                        .filter((val) => {
                            if (
                                searchTerm === '' ||
                                val.topicName.toLowerCase().includes(searchTerm.toLowerCase())
                            )
                                return val;
                            return '';
                        })
                        .map((item, i) => {
                            return (
                                <TopicCard
                                    topicInfo={item}
                                    key={i}
                                    setCurrentTopicId={setCurrentTopicId}
                                    isParent={isParent}
                                />
                            );
                        })}
                </div>
            </div>
            {/* right */}
            <div className='flex flex-col gap-5 w-[60%] bg-white py-5 px-10 overflow-y-auto'>
                {/* Topic tile */}
                {currentTopic ? (
                    <>
                        <div className='flex flex-row gap-3 justify-center items-center'>
                            <span className='text-2xl text-primary'>{currentTopic?.topicName}</span>
                        </div>
                        {/* image */}
                        <div
                            className={`relative rounded-lg min-h-[300px] overflow-hidden flex items-center justify-center bg-center w-full select-none cursor-pointer transition-all`}
                        >
                            <img
                                src={currentTopic?.topicImg || topicImage}
                                className='h-[300px] object-cover w-full'
                                alt=''
                            />
                        </div>
                        {/* topic des */}
                        <div className='flex flex-col gap-3'>
                            <div className='flex flex-row justify-between items-center'>
                                <span className='text-2xl font-medium text-gray-700'>
                                    Topic descriptions
                                </span>
                            </div>
                            {currentTopic?.description && (
                                <span className='px-2 text-justify'>
                                    {currentTopic?.description}
                                </span>
                            )}
                        </div>
                        {/* skills */}
                        <div className='flex flex-col gap-3'>
                            <div className='flex flex-row justify-between items-center'>
                                <span className='text-2xl font-medium text-gray-700'>Skills</span>
                            </div>
                            <div className='flex flex-col gap-1'>
                                {skills.map((val, i) => {
                                    return (
                                        <SkillInTopics
                                            val={val}
                                            key={val.id + i}
                                            getTopicOfClass={getTopicOfClass}
                                            isParent={isParent}
                                            studentInfo={studentInfo}
                                        />
                                    );
                                })}
                            </div>
                        </div>
                    </>
                ) : (
                    <div className='w-full h-full flex flex-col gap-5 items-center justify-center'>
                        <img src={nocontent} alt='' className='h-[300px]' />
                        <span className='text-2xl font-[500] text-primary'>
                            No content, choose 1 topic to view
                        </span>
                    </div>
                )}
            </div>
            <TokenExpire isOpen={isExpired} />
        </div>
    );
};

export default StudentClass;
